/**
 * PetCareApp - Disease Alert Service
 * API dla alert√≥w epidemiologicznych z integracjƒÖ (WOAH, ADNS, GIW, EFSA)
 * @author VS
 */

import { diseaseAlertApi } from './api';

export const diseaseAlertService = {
    /**
     * Pobranie alert√≥w z zewnƒôtrznych ≈∫r√≥de≈Ç
     */
    async getAlerts(filters = {}) {
        const params = new URLSearchParams();
        if (filters.country) params.append('country', filters.country);
        if (filters.source) params.append('source', filters.source);
        if (filters.status) params.append('status', filters.status);
        if (filters.severity) params.append('severity', filters.severity);
        
        const response = await diseaseAlertApi.get(`/alerts?${params}`);
        return response.data;
    },

    /**
     * Pobranie dostƒôpnych ≈∫r√≥de≈Ç danych
     */
    async getSources() {
        const response = await diseaseAlertApi.get('/alerts/sources');
        return response.data;
    },

    /**
     * Pobranie listy monitorowanych chor√≥b
     */
    async getDiseases() {
        const response = await diseaseAlertApi.get('/alerts/diseases');
        return response.data;
    },

    /**
     * Pobranie informacji o strefach ASF
     */
    async getASFZones() {
        const response = await diseaseAlertApi.get('/alerts/asf');
        return response.data;
    },

    /**
     * Pobranie szczeg√≥≈Ç√≥w alertu
     */
    async getAlertById(alertId) {
        const response = await diseaseAlertApi.get(`/alerts/${alertId}`);
        return response.data;
    },

    /**
     * Pobranie aktywnych alert√≥w
     */
    async getActiveAlerts() {
        const response = await diseaseAlertApi.get('/alerts/active');
        return response.data;
    },

    /**
     * Alerty w pobli≈ºu lokalizacji
     */
    async getNearbyAlerts(lat, lng, radius = 100) {
        const response = await diseaseAlertApi.get(`/alerts/nearby?lat=${lat}&lng=${lng}&radius=${radius}`);
        return response.data;
    },

    /**
     * Pobranie bazy chor√≥b
     */
    async getDiseases(filters = {}) {
        const params = new URLSearchParams();
        if (filters.category) params.append('category', filters.category);
        if (filters.zoonotic !== undefined) params.append('zoonotic', filters.zoonotic);
        if (filters.notifiable !== undefined) params.append('notifiable', filters.notifiable);
        
        const response = await diseaseAlertApi.get(`/diseases?${params}`);
        return response.data;
    },

    /**
     * Szczeg√≥≈Çy choroby
     */
    async getDiseaseById(diseaseId) {
        const response = await diseaseAlertApi.get(`/diseases/${diseaseId}`);
        return response.data;
    },

    /**
     * Statystyki alert√≥w
     */
    async getStats() {
        const response = await diseaseAlertApi.get('/stats');
        return response.data;
    },

    /**
     * Lista wojew√≥dztw
     */
    async getRegions() {
        const response = await diseaseAlertApi.get('/regions');
        return response.data;
    }
};

export default diseaseAlertService;
